import { useHttp } from "../../hooks/http.hook";
import { useState, useEffect, useMemo } from "react";
import { useSelector } from "react-redux";
import FeelingsItem from "../FeelingsItem/FeelingsItem";
import setContent from "../../utils/setContent";

const Feelings = () => {
   const [feelings, setFeelings] = useState([]);
   const userInfo = useSelector((state) => state.user);

   const { nickName } = userInfo;

   const { request, process, setProcess } = useHttp();

   useEffect(() => {
      request(
         {
            method: "GET",
            url: "http://mskko2021.mad.hakta.pro/api/feelings",
            data: "",
         },
         function printResult(result) {
            setFeelings((feelings) => result.data);
            setProcess("confirmed");
         }
      );
      // eslint-disable-next-line
   }, []);

   const renderItems = (arr) => {
      if (arr.length === 0) {
         return <div>Данных пока нет.</div>;
      }

      return arr.map(({ id, ...props }) => {
         return <FeelingsItem key={id} {...props} />;
      });
   };

   const elements = useMemo(() => {
      return setContent(process, () => renderItems(feelings), feelings);
      // eslint-disable-next-line
   }, [process]);

   return (
      <div className="feelings">
         <h1>С возвращением, {nickName}!</h1>
         <div className="feelings__subtitle">
            Каким ты себя ощущаешь сегодня?
         </div>
         <ul className="feelings__row">{elements}</ul>
      </div>
   );
};

export default Feelings;
