const QuotesItem = ({ image, title, description }) => {
   return (
      <li className="quotes__column">
         <div
            className="quotes__item item-quotes"
            style={{
               background: `#F7F3F0 url(${image}) no-repeat right center`,
            }}
         >
            <div>
               <h1>{title}</h1>
               <p>{description}</p>
               <button className="btn btn-about">подробнее</button>
            </div>
         </div>
      </li>
   );
};

export default QuotesItem;
