import { useMemo, useCallback } from "react";
import ImageItem from "../ImageItem/ImageItem";
import { useDispatch, useSelector } from "react-redux";
import { setImages } from "../../store/slices/ImagesSlice";
import { v4 as uuidv4 } from "uuid";
import { useAlert } from "react-alert";

const ImageGallery = () => {
   const alert = useAlert();
   const dispatch = useDispatch();
   const today = new Date();
   const { imagesArray } = useSelector((state) => state.images);

   const handleAddImage = useCallback((event) => {
      if (event.target.files[0] !== undefined) {
         dispatch(
            setImages({
               imagesArray: {
                  id: uuidv4(),
                  image: URL.createObjectURL(event.target.files[0]),
                  time: today.getHours() + ":" + today.getMinutes(),
               },
            })
         );
         alert.success("Изображение добавлено");
      }
      // eslint-disable-next-line
   }, []);

   const renderItems = (arr) => {
      if (arr.length === 0) {
         return <div>Изображений пока нет.</div>;
      }

      return arr.map(({ id, ...props }) => {
         return <ImageItem key={id} imageID={id} {...props} />;
      });
   };

   const elements = useMemo(() => {
      return renderItems(imagesArray);
      // eslint-disable-next-line
   }, [imagesArray]);

   return (
      <div className="image-gallery">
         <ul className="image-gallery__list">
            {elements}
            <label className="image-gallery__item item-image-gallery add">
               <input
                  type="file"
                  name="myImage"
                  onChange={(event) => handleAddImage(event)}
               />
               <p>+</p>
            </label>
         </ul>
      </div>
   );
};

export default ImageGallery;
