import svg from "../../resourses/svg/sprites.svg";
import leafs from "../../resourses/img/leafs.png";
import { useNavigate, Link } from "react-router-dom";
import AuthForm from "../AuthForm/AuthForm";
import { useDispatch } from "react-redux";
import { setUser } from "../../store/slices/userSlice";
import { useAuth } from "../../hooks/useAuth.hook";
import { useCallback } from "react";
import { useHttp } from "../../hooks/http.hook";
import Helmet from "react-helmet";
import { useAlert } from "react-alert";

const LoginPage = () => {
   const alert = useAlert();
   const navigate = useNavigate();
   const dispatch = useDispatch();
   const { isAuth } = useAuth();

   const { request } = useHttp();

   const handleLogin = useCallback((email, password) => {
      if (
         (email === "wsr" && password === "wsr") ||
         (email === "junior@wsr.ru" && password === "junior") ||
         (email === "general@wsr.ru" && password === "general")
      ) {
         request(
            {
               method: "POST",
               url: "http://mskko2021.mad.hakta.pro/api/user/login",
               data: JSON.stringify({
                  email,
                  password,
               }),
            },
            function printResult(result) {
               if (result !== "error") {
                  dispatch(
                     setUser({
                        email: result.email,
                        id: result.id,
                        token: result.token,
                        avatar: result.avatar,
                        nickName: result.nickName,
                     })
                  );
                  navigate("/main");
               } else {
                  alert.error(
                     "Что-то не так. Возможно, вы не получили доступ."
                  );
                  navigate("/request-access");
               }
            }
         );
      } else {
         alert.error("Данные введены некорректно");
         console.log(
            "Ну... данные введены с ошибкой, а ошибку POST в консоль я не покажу))"
         );
      }
      // eslint-disable-next-line
   }, []);

   return (
      <main>
         <div
            className="login-page"
            style={{
               background: `url(${leafs}) no-repeat bottom center`,
               backgroundSize: "contain",
            }}
         >
            <Helmet>
               <meta
                  name="description"
                  content={`MAD-Meditation - Авторизация`}
               />
               <title>Авторизация</title>
            </Helmet>
            <div className="login-page__container _container">
               <div className="login-page__body">
                  <Link to="/main" className="login-page__logo logo">
                     <svg>
                        <use href={`${svg}#logo`}></use>
                     </svg>
                  </Link>
                  <h1>Авторизация</h1>
                  <AuthForm handleClick={handleLogin} btnValue="Войти" />
                  <Link to="/soon/Тут будет регистрация">Регистрация</Link>
                  {isAuth ? (
                     <button
                        className="btn btn-primary"
                        onClick={() => navigate("/profile")}
                     >
                        Профиль
                     </button>
                  ) : null}
               </div>
            </div>
         </div>
      </main>
   );
};

export default LoginPage;
