import { useNavigate } from "react-router-dom";

const ImageItem = ({ imageID, image, time }) => {
   const navigate = useNavigate();

   return (
      <li
         className="image-gallery__item item-image-gallery"
         onClick={() => navigate(`/full-image/${imageID}`)}
      >
         <img src={image} alt="" />
         <span>{time}</span>
      </li>
   );
};

export default ImageItem;
