import bg from "../../resourses/img/welcome-bg.jpg";
import svg from "../../resourses/svg/sprites.svg";
import { useNavigate, Link } from "react-router-dom";
import Helmet from "react-helmet";

const OnboardingPage = () => {
   const navigate = useNavigate();

   return (
      <main>
         <div
            className="onboarding-page"
            style={{
               background: `url(${bg}) center center`,
               backgroundSize: "cover",
            }}
         >
            <Helmet>
               <meta
                  name="description"
                  content={`MAD-Meditation - Onboarding`}
               />
               <title>Onboarding</title>
            </Helmet>
            <div className="onboarding-page__container _container">
               <div className="onboarding-page__body">
                  <div className="onboarding-page__logo logo">
                     <svg>
                        <use href={`${svg}#logo`}></use>
                     </svg>
                  </div>
                  <h1>Привет</h1>
                  <div className="onboarding-page__phrases">
                     <p>
                        Наслаждайся отборочными. <br />
                        Будь внимателен. <br />
                        Делай хорошо.
                     </p>
                  </div>
                  <div className="onboarding-page__auth">
                     <button
                        className="btn btn-primary"
                        onClick={() => navigate("/login")}
                     >
                        Войти в аккаунт
                     </button>
                     <div className="no-account">
                        Еще нет аккаунта?{" "}
                        <Link to="/soon/Тут будет регистрация">
                           Зарегистрируйтесь
                        </Link>
                     </div>
                  </div>
               </div>
            </div>
         </div>
      </main>
   );
};

export default OnboardingPage;
