import { useParams } from "react-router-dom";
import Helmet from "react-helmet";
import NavbarMenuBottom from "../NavbarMenuBottom/NavbarMenuBottom";

const EmptyPage = () => {
   let { text } = useParams();

   if (!text) {
      text = "404 - Такой страницы не существует.";
   }

   return (
      <main>
         <div className="empty-page">
            <Helmet>
               <meta name="description" content={`MAD-Meditation - ${text}`} />
               <title>{text}</title>
            </Helmet>
            <p>{text}</p>
         </div>
         <NavbarMenuBottom />
      </main>
   );
};

export default EmptyPage;
