import { useHttp } from "../../hooks/http.hook";
import { useState, useEffect, useMemo } from "react";
import QuotesItem from "../QuotesItem/QuotesItem";
import setContent from "../../utils/setContent";

const Quotes = () => {
   const [quotes, setQuotes] = useState([]);

   const { request, process, setProcess } = useHttp();

   useEffect(() => {
      request(
         {
            method: "GET",
            url: "http://mskko2021.mad.hakta.pro/api/quotes",
            data: "",
         },
         function printResult(result) {
            setQuotes((quotes) => result.data);
            setProcess("confirmed");
         }
      );
      // eslint-disable-next-line
   }, []);

   const renderItems = (arr) => {
      if (arr.length === 0) {
         return <div>Данных пока нет.</div>;
      }

      return arr.map(({ id, ...props }) => {
         return <QuotesItem key={id} {...props} />;
      });
   };

   const elements = useMemo(() => {
      return setContent(process, () => renderItems(quotes), quotes);
      // eslint-disable-next-line
   }, [process]);

   return (
      <div className="quotes">
         <ul className="quotes__row">{elements}</ul>
      </div>
   );
};

export default Quotes;
