import { useDispatch } from "react-redux";
import { useCallback } from "react";
import { removeImages } from "../../store/slices/ImagesSlice";
import { useNavigate } from "react-router-dom";
import { useAlert } from "react-alert";

const ImageNavbarControlMenuBottom = ({ imageId }) => {
   const alert = useAlert();
   const dispatch = useDispatch();
   const navigate = useNavigate();

   const handleDeleteImage = useCallback((id) => {
      dispatch(removeImages({ id }));
      navigate(`/profile`);
      alert.success("Изображение удалено");
      // eslint-disable-next-line
   }, []);

   return (
      <div className="image-navbar-control-menu-bottom">
         <button className="btn" onClick={() => handleDeleteImage(imageId)}>
            удалить
         </button>
         <button className="btn" onClick={() => navigate(`/profile`)}>
            закрыть
         </button>
      </div>
   );
};

export default ImageNavbarControlMenuBottom;
