import { useParams, Navigate } from "react-router-dom";
import { useSelector } from "react-redux";
import { useAuth } from "../../hooks/useAuth.hook";
import Helmet from "react-helmet";
import ImageNavbarControlMenuBottom from "../ImageNavbarControlMenuBottom/ImageNavbarControlMenuBottom";

const FullImagePage = () => {
   const { isAuth } = useAuth();
   const { imageId } = useParams();
   const { imagesArray } = useSelector((state) => state.images);

   return isAuth && imagesArray.length !== 0 ? (
      <>
         <Helmet>
            <meta
               name="description"
               content={`MAD-Meditation - Изображение ${imageId}`}
            />
            <title>Изображение - {imageId}</title>
         </Helmet>
         <main>
            <div className="full-image-page">
               {
                  // eslint-disable-next-line
                  imagesArray.map(({ id, image }) => {
                     if (imageId === id)
                        return <img key={id} src={image} alt="" />;
                  })
               }
               <ImageNavbarControlMenuBottom imageId={imageId} />
            </div>
         </main>
      </>
   ) : (
      <Navigate to="/login" />
   );
};

export default FullImagePage;
