import { useSelector, useDispatch } from "react-redux";
import { useNavigate, Link } from "react-router-dom";
import svg from "../../resourses/svg/sprites.svg";
import { useCallback } from "react";
import { removeUser } from "../../store/slices/userSlice";

const Header = ({ location = "main" }) => {
   const dispatch = useDispatch();
   const navigate = useNavigate();
   const userInfo = useSelector((state) => state.user);

   const { avatar, nickName } = userInfo;

   const handleExit = useCallback(() => {
      dispatch(removeUser());
      navigate("/login");
      // eslint-disable-next-line
   }, []);

   return (
      <header className="header">
         <div className="header__container _container">
            <div className="header__body">
               <Link to="/soon/Тут будет меню" className="header__burger">
                  <svg>
                     <use href={`${svg}#burger`}></use>
                  </svg>
               </Link>
               <Link to="/main" className="header__logo logo">
                  <svg>
                     <use href={`${svg}#logo`}></use>
                  </svg>
               </Link>
               {location === "main" ? (
                  <Link to="/profile" className="header__avatar">
                     <img src={avatar} alt={nickName} />
                  </Link>
               ) : (
                  <button className="btn" onClick={() => handleExit()}>
                     Выход
                  </button>
               )}
            </div>
         </div>
      </header>
   );
};

export default Header;
