import svg from "../../resourses/svg/sprites.svg";
import { NavLink } from "react-router-dom";

const NavbarMenuBottom = () => {
   return (
      <div className="navbar-menu-bottom">
         <ul className="navbar-menu-bottom__list">
            <li className="navbar-menu-bottom__item">
               <NavLink to="/main">
                  <svg>
                     <use href={`${svg}#logo`}></use>
                  </svg>
               </NavLink>
            </li>
            <li className="navbar-menu-bottom__item">
               <NavLink to="/soon/Тут будет прослушивание.">
                  <svg>
                     <use href={`${svg}#listen`}></use>
                  </svg>
               </NavLink>
            </li>
            <li className="navbar-menu-bottom__item">
               <NavLink to="/profile">
                  <svg>
                     <use href={`${svg}#profile`}></use>
                  </svg>
               </NavLink>
            </li>
         </ul>
      </div>
   );
};

export default NavbarMenuBottom;
