import { useSelector } from "react-redux";

const UserInfo = () => {
   const userInfo = useSelector((state) => state.user);

   const { avatar, nickName } = userInfo;

   return (
      <div className="user-info">
         <img src={avatar} alt={nickName} />
         <h1>{nickName}</h1>
      </div>
   );
};

export default UserInfo;
