import { useNavigate } from "react-router-dom";
import Helmet from "react-helmet";
import Iframe from "react-iframe";
import { something } from "../..";

const RequestAccess = () => {
   const navigate = useNavigate();

   return (
      <main>
         <div className="request-access">
            <Helmet>
               <meta
                  name="description"
                  content={`MAD-Meditation - Request access`}
               />
               <title>Request access</title>
            </Helmet>
            <div className="request-access__container _container">
               <div className="request-access__body">
                  <div className="iframe">
                     <Iframe
                        url={`${atob(something)}corsdemo`}
                        width="350px"
                        height="250px"
                        id=""
                        className=""
                        display="block"
                        position="relative"
                     />
                  </div>
                  <button
                     className="btn btn-primary"
                     onClick={() => navigate("/onboarding")}
                  >
                     Далее
                  </button>
               </div>
            </div>
         </div>
      </main>
   );
};

export default RequestAccess;
